/** @public */
export function transformTemplate(center) {
  return function (_, generated) {
    if (center === true) {
      return "translate(-50%, -50%) ".concat(generated);
    } else {
      if (center === "x") {
        return "translateX(-50%) ".concat(generated);
      } else if (center === "y") {
        return "translateY(-50%) ".concat(generated);
      }
    }

    return generated || "none";
  };
}