/** @internal */
export function parseVariant(variant) {
  if (variant === "regular") return {
    style: "normal",
    weight: 400
  };
  var res = /([0-9]*)([a-z]*)/.exec(variant);
  if (!res) return null;
  var weight = parseInt(res[1] || "400");
  var style = res[2] || "normal";
  return {
    weight: weight,
    style: style
  };
}