import { BackgroundImage } from "../types/BackgroundImage.js";
import { Animatable } from "../../animation/Animatable/Animatable.js";
/** @internal */

export function imagePatternPropsForFill(fill, frame, id) {
  fill = Animatable.get(fill, "#09F");
  if (!BackgroundImage.isImageObject(fill)) return undefined;
  if (!fill.pixelWidth || !fill.pixelHeight) return undefined;
  var imageWidth = fill.pixelWidth;
  var imageHeight = fill.pixelHeight;
  var transform;
  var _fill = fill,
      fit = _fill.fit;

  if (fit === "fill" || fit === "fit" || !fit) {
    var scaleX = 1;
    var scaleY = 1;
    var offsetX = 0;
    var offsetY = 0;
    var imageRatio = imageWidth / imageHeight;
    var realWidth = frame.height * imageRatio;
    var realHeight = frame.width / imageRatio;
    var validScaleX = realWidth / frame.width;
    var validScaleY = realHeight / frame.height;

    if (fit === "fill" || !fit ? validScaleY > validScaleX : validScaleY < validScaleX) {
      scaleY = validScaleY;
      offsetY = (1 - validScaleY) / 2;
    } else {
      scaleX = validScaleX;
      offsetX = (1 - validScaleX) / 2;
    }

    transform = "translate(".concat(offsetX, ", ").concat(offsetY, ") scale(").concat(scaleX, ", ").concat(scaleY, ")");
  }

  var imageId = "id".concat(id, "g", "-fillImage");
  return {
    id: imageId,
    path: fill.src,
    transform: transform
  };
}